import React from "react";
import firebase from "../../config";
// import 'react-tabulator/lib/styles.css';
import { ReactTabulator } from "react-tabulator";
// import { CSVLink } from "react-csv";

class UserListScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      values: [],
      columns: [
        { title: "ID", field: "id", width: 100},
        { title: "Email", field: "Email",headerFilter: "input"},
        { title: "First Name", field: "FirstName", },
        { title: "Last Name", field: "LastName", },
        { title: "Phone", field: "Phone", },
        { title: "Total Revenue", field: "totalMoney", },
        { title: "Revenue", field: "payMoney", },
        { title: "Wallet", field: "wallMoney", }
      ],
      // csv: [
      //   { label: "ID", key: "id"},
      //   { label: "Email", key: "Email"},
      //   { label: "First Name", key: "FirstName" },
      //   { label: "Last Name", key: "LastName" },
      //   { label: "Phone", key: "Phone" },
      //   { label: "Total Revenue", key: "totalMoney"},
      //   { label: "Revenue", key: "payMoney" },
      //   { label: "Wallet", key: "wallMoney" }
      // ]
    };
  }
  componentDidMount() {
    firebase.database().ref('users').on('value', snapshot =>{
        let list =[];
        snapshot.forEach(snap =>{
            list.push(snap.val());
        });
        console.log(list);
        this.setState({values: list});
    })
  }

  render() {
    return (
      // <div className="d-flex justify-content-center align-items-start" style={{width:'95vw',height:'100vh',backgroundColor:'#f6f7fb'}}>
      //   <div className="bg-primary col-12 d-flex justify-content-end align-items-center"style={{height:60}}>
      //     <div type="button" className="rounded-circle d-flex justify-content-center align-items-center" style={{width:40,height:40,backgroundColor:''}}>
      //           <img src={require('../../img/PANTO.png')} style={{width:20}}></img>
      //     </div>
      //   </div>
        
      // </div>
      <div className="d-flex justify-content-start align-items-start flex-column" style={{width:'95vw',height:'100vh',backgroundColor:'#f6f7fb'}}>
        <div className="bg-primary d-flex justify-content-between align-items-center" style={{width:'95vw',height:60}}>
          <span className="font-weight-bold pl-4" style={{fontSize:20,color:'white'}}>USER LIST</span>
        <div type="button" className="rounded-circle d-flex justify-content-center align-items-center mr-4" style={{width:40,height:40,backgroundColor:'white'}}>
            <img src={require('../../img/PANTO.png')} style={{width:20}}></img>
        </div>
        </div>
          <ReactTabulator
          style={{width:'100%'}}
          className="bg-primary"
          data={this.state.values}
          columns={this.state.columns}
          tooltips={true}
          layout={"fitData"}
        />
      </div>
    );
  }
}
export default UserListScreen;
