import React from 'react';
import './styles.css';
import { BrowserRouter, Route, Switch, NavLink,HashRouter } from 'react-router-dom';
import DashScreen from "../Screen/DashScreen/DashScreen";
import UserListScreen from "../Screen/UserListScreen/UserListScreen";
import PayOutScreen from "../Screen/PayOutScreen/PayOutScreen"

class Navigater extends React.Component {

  render() {
    return (
       <HashRouter basename="/">
        <div className="wrapper d-flex">
          <div className="sidebar">
            <div className="sidebar-header d-flex justify-content-center align-items-center">
              <img src={require('../img/PANTO.png')} alt="" className="logo" />
            </div>
            <ul className="list-unstyled components d-flex flex-column align-items-center mt-5">
              <li className="sidebar-opt mb-4">
                <NavLink className="sidebarlink nav-link p-3 fa fa-slack text-primary" exact  to={{ pathname: `/` }} activeStyle={{ color: 'white' }} data-toggle="tooltip" data-placement="right" title="DashBoard" />
              </li>
              <li className="sidebar-opt mb-4">
                <NavLink className="sidebarlink nav-link p-3 fa fa-lastfm text-primary" to={{ pathname: `/userList` }} activeStyle={{ color: 'white' }} data-toggle="tooltip" data-placement="right" title="User List" />
              </li>
              <li className="sidebar-opt mb-4">
                <NavLink className="sidebarlink nav-link p-3 fa fa-credit-card-alt text-primary" to={{ pathname: `/payOut` }} activeStyle={{ color: 'white' }} data-toggle="tooltip" data-placement="right" title="Lead" />
              </li> 
            </ul>
          </div>
          <div>
            <Switch>
            <Route exact  path="/" component={DashScreen}/>
            <Route path="/userList"component={UserListScreen}/>
            <Route path="/payOut" component={PayOutScreen}/>
            </Switch>
          </div>
        </div>
      </HashRouter>
    )
  }
}

export default Navigater;
